body {
    -webkit-animation: colorchange 20s infinite;
            animation: colorchange 20s infinite; 
      
}
@-webkit-keyframes colorchange
{
  0%   {background: red;}
  25%  {background: rgb(219, 205, 10);}
  50%  {background: rgb(255, 0, 0);}
  75%  {background: rgb(219, 205, 10);}
  100% {background: rgb(255, 30, 0);}
}
@keyframes colorchange
{
  0%   {background: red;}
  25%  {background: rgb(219, 205, 10);}
  50%  {background: rgb(255, 0, 0);}
  75%  {background: rgb(219, 205, 10);}
  100% {background: rgb(255, 30, 0);}
}
.topBar {
    margin-top: 0px;
    margin-bottom: 20px;
    color: white;
    background: black;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 40px;
    padding-right: 40px;
    position: fixed;
    z-index: 99;
    top: 0px;
    margin-left: 0px;
    width: 100%;

}

.topBar #homeLink {
    color: white;
    font-size: 35px;
}

header {
    background: rgb(211, 208, 8);
    margin-bottom: 30px;
}

.title{
    color: rgb(5, 0, 0);
    text-shadow: -1px 0 rgb(253, 3, 3), 0 2px rgb(255, 2, 2), 2px 0 rgb(253, 5, 5), 0 -1px rgb(248, 8, 8);
    text-align: center;
    font-size: 85px;
    font-family: 'Slabo 27px', serif;
    margin-top: 100px;
    padding-top: 40px;
    padding-bottom: 30px;
    font-weight: bolder;

}

.instructions {
    font-size: 35px;
    text-align: center;
    padding-bottom: 70px;
    font-weight: bold;
}

@media only screen and (max-width: 360px) {
    .title{
        font-size: 45px;
        margin-top: 250px;    
    }
    .instructions {
        font-size: 25px;
        margin-left: 25px;
        margin-right: 25px;
    }
}
.img-container {
    width: 155px;
    height: 155px;
    box-shadow: 0 3px 6px rgb(252, 251, 251), 0 3px 6px rgb(236, 234, 234);
    border: 3px solid rgb(253, 237, 7);
  }

.card {
    width: 155px;
    height: 155px;
    margin: 15px 45px;
}  

@media only screen and (max-width: 360px) {
  .img-container {
    width: 95px;
    height: 95px;
  }
  .card {
    width: 100px;
    height: 100px;
    margin-left: 20px;
    margin-top: 20px;
    table-layout: fixed;
    }  
  .images {
    width: 90px;
    height: 90px;
    table-layout: fixed;
  }
  
}
.cardBox {
    margin-bottom: 50px;
}
#footerText {
    text-align: center;
    font-size: 20px;
}
.page-footer {
    background: black;
    color: white;
    padding-top: 20px;
    Padding-bottom: 20px;
}
